.Rating_rating__3LEfv {
    border: none;
    float: left;
}

.Rating_rating__3LEfv > input {
    display: none;
}

.Rating_rating__3LEfv > label:before {
    margin: 5px;
    font-size: 1.25em;
    font-family: FontAwesome;
    display: inline-block;
    content: "\f005";
}

.Rating_rating__3LEfv > .Rating_half__Y-QO9:before {
    content: "\f089";
    position: absolute;
}

.Rating_rating__3LEfv > label {
    color: #ddd;
    float: right;
}

/***** CSS Magic to Highlight Stars on Hover *****/

.Rating_rating__3LEfv > input:checked ~ label, 
.Rating_rating__3LEfv:not(:checked) > label:hover, 
.Rating_rating__3LEfv:not(:checked) > label:hover ~ label {
    color: #FFD700;
}

/* hover previous stars in list */

.Rating_rating__3LEfv > input:checked + label:hover, 
.Rating_rating__3LEfv > input:checked ~ label:hover,
.Rating_rating__3LEfv > label:hover ~ input:checked ~ label, 
.Rating_rating__3LEfv > input:checked ~ label:hover ~ label {
    color: #FFED85;
}

.Rating_rating__3LEfv:disabled > input:checked + label:hover, 
.Rating_rating__3LEfv:disabled > input:checked ~ label:hover,
.Rating_rating__3LEfv:disabled > label:hover ~ input:checked ~ label, 
.Rating_rating__3LEfv:disabled > input:checked ~ label:hover ~ label {
    color: #ddd;
}
.Rating_rating__3LEfv:disabled > input:checked ~ label, 
.Rating_rating__3LEfv:disabled:not(:checked) > label:hover, 
.Rating_rating__3LEfv:disabled:not(:checked) > label:hover ~ label {
    color: #ddd;
}

