.Heart_likebutton__2s_F6 {
    display: inline-block;
    position: relative;
    font-size: 1rem;
    cursor: pointer;
    margin-top: 0.6875rem;
    margin-left: 1.25rem;
}

.Heart_likebutton__2s_F6::before {
    font-size: 3em;
    color: #4c545d;
    content: '♥';
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.Heart_likebutton__2s_F6::after {
    font-size: 3em;
    color: #ff3252;
    content: '♥';
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%) scale(0);
    transform: translate(-50%, -50%) scale(0);
    transition: -webkit-transform 0.2s;
    -webkit-transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
}

.Heart_likebutton__2s_F6.Heart_liked__20o7q::after {
    -webkit-transform: translate(-50%, -50%) scale(1.1);
    transform: translate(-50%, -50%) scale(1.1);
}


.Heart_likebutton__2s_F6:disabled:after, .Heart_likebutton__2s_F6.Heart_liked__20o7q:disabled:after{
    color: #4c545d;
}
